///<reference path="AudioProcessor.ts" />
import {AudioProcessor} from "audioProcessor"

export class AudioFileParser {
  fileReader: any;
  audioProcessor: AudioProcessor;
    
  constructor(processor: AudioProcessor){
    this.audioProcessor = processor;
    this.fileReader = new FileReader();
  }
    
	public parse(file: any){	
        this.fileReader.onload = this.onFileLoaded;
        this.fileReader.onerror = this.onFileLoadFailed;              
        this.fileReader.readAsArrayBuffer(file);
	}
  
  private onFileLoaded  = (e) => {
     var fileResult = (<any>e.target).result;
     this.audioProcessor.start(fileResult);
  }
  
  private onFileLoadFailed = (e) => {
    debugger
  }
}